@import 'variables';
@import 'fonts';


.page {
  padding: 0px;
  margin: 0px;
  font-family: "ProximaNova", serif;
  font-weight: 400; }

.h2 {
  font-family: "Furore", serif;
  font-size: 30px;
  line-height: 36px;
  font-weight: normal;
  color: black;
  position: relative;
  &::after {
    content: "";
    display: block;
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    height: 2px;
    width: 50px;
    background: black; } }

ul {
  padding-left: 0px; }
li {
  list-style-type: none; }
*:focus {
  outline-width: 1px;
  outline-style: solid;
  outline-color: blue; }


@import 'header';
@import 'portfolio';
@import 'skills';
